.resume {
    font-family: Arial, sans-serif;
    max-width: 800px;
    margin: 20px auto;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
}

.resume section {
    margin-bottom: 20px;
}

.resume h2 {
    color: #333;
}

.resume h3 {
    color: #555;
}

.resume p {
    color: #666;
}

.resume ul {
    list-style-type: circle;
    padding-left: 20px;
}

.resume li {
    margin-bottom: 5px;
}
